___CSS_LOADER_EXPORT___.push([module.id, `.proyectos-contain {
    text-align: center;
  }
  
  .proyectos-container {
    display: flex;
    flex-wrap: wrap;
    gap: 6rem;
    justify-content: center;
    padding: 20px;
  }
  
  .proyectos-title {
    font-size: 4rem;
    color: #ffffff;
    margin: 5rem 0 2rem 0;
    animation: slideIn 0.5s ease;
    font-family: "DM Serif Display", serif;
    border-bottom: .4rem solid #ff6600;
    display: inline-block;
  }
  
  @keyframes slideIn {
    from {
        transform: translateY(-20px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
  }
  
  @keyframes slideInRight {
    from {
        transform: translateX(100px);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
  }
  
  .fade-in.slide-right {
    animation: slideInRight 0.6s ease;
  }
  
  .projects-icon {
    color: #ff6600;
    font-size: 3.5rem;
    vertical-align: middle;
    margin-bottom: 10px;
  }
  
  @media (max-width: 760px) {
  
    .proyectos-contain {
        padding: none;
    }
  
    .proyectos-container {
        flex-direction: column;
        gap: 2rem;
        padding: 10px;
    }
  
    .proyectos-title {
        font-size: 2.5rem;
        margin: 2rem 0 1rem 0;
        border-bottom: 0.3rem solid #ff6600;
    }
  
    .projects-icon {
        font-size: 2.5rem;
        margin-bottom: 8px;
    }
  
    @keyframes slideIn {
        from {
            transform: translateY(-10px);
            opacity: 0;
        }
        to {
            transform: translateY(0);
            opacity: 1;
        }
    }
  
    .fade-in.slide-right {
        animation: slideInRight 0.5s ease;
    }
  }`, "",{"version":3,"sources":["webpack://./src/styles/proyectos.css"],"names":[],"mappings":"AAAA;IACI,kBAAkB;EACpB;;EAEA;IACE,aAAa;IACb,eAAe;IACf,SAAS;IACT,uBAAuB;IACvB,aAAa;EACf;;EAEA;IACE,eAAe;IACf,cAAc;IACd,qBAAqB;IACrB,4BAA4B;IAC5B,sCAAsC;IACtC,kCAAkC;IAClC,qBAAqB;EACvB;;EAEA;IACE;QACI,4BAA4B;QAC5B,UAAU;IACd;IACA;QACI,wBAAwB;QACxB,UAAU;IACd;EACF;;EAEA;IACE;QACI,4BAA4B;QAC5B,UAAU;IACd;IACA;QACI,wBAAwB;QACxB,UAAU;IACd;EACF;;EAEA;IACE,iCAAiC;EACnC;;EAEA;IACE,cAAc;IACd,iBAAiB;IACjB,sBAAsB;IACtB,mBAAmB;EACrB;;EAEA;;IAEE;QACI,aAAa;IACjB;;IAEA;QACI,sBAAsB;QACtB,SAAS;QACT,aAAa;IACjB;;IAEA;QACI,iBAAiB;QACjB,qBAAqB;QACrB,mCAAmC;IACvC;;IAEA;QACI,iBAAiB;QACjB,kBAAkB;IACtB;;IAEA;QACI;YACI,4BAA4B;YAC5B,UAAU;QACd;QACA;YACI,wBAAwB;YACxB,UAAU;QACd;IACJ;;IAEA;QACI,iCAAiC;IACrC;EACF","sourcesContent":[".proyectos-contain {\n    text-align: center;\n  }\n  \n  .proyectos-container {\n    display: flex;\n    flex-wrap: wrap;\n    gap: 6rem;\n    justify-content: center;\n    padding: 20px;\n  }\n  \n  .proyectos-title {\n    font-size: 4rem;\n    color: #ffffff;\n    margin: 5rem 0 2rem 0;\n    animation: slideIn 0.5s ease;\n    font-family: \"DM Serif Display\", serif;\n    border-bottom: .4rem solid #ff6600;\n    display: inline-block;\n  }\n  \n  @keyframes slideIn {\n    from {\n        transform: translateY(-20px);\n        opacity: 0;\n    }\n    to {\n        transform: translateY(0);\n        opacity: 1;\n    }\n  }\n  \n  @keyframes slideInRight {\n    from {\n        transform: translateX(100px);\n        opacity: 0;\n    }\n    to {\n        transform: translateX(0);\n        opacity: 1;\n    }\n  }\n  \n  .fade-in.slide-right {\n    animation: slideInRight 0.6s ease;\n  }\n  \n  .projects-icon {\n    color: #ff6600;\n    font-size: 3.5rem;\n    vertical-align: middle;\n    margin-bottom: 10px;\n  }\n  \n  @media (max-width: 760px) {\n  \n    .proyectos-contain {\n        padding: none;\n    }\n  \n    .proyectos-container {\n        flex-direction: column;\n        g