    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100%;
    z-index: -1;
    background-color: rgb(0, 0, 0); 
    object-fit: cover;
    overflow: hidden;
}

::-webkit-scrollbar {
    width: 12px;
    height: 12px;
}

::-webkit-scrollbar-track {
    background: rgb(0, 0, 0); 
    border-left: 1px solid rgb(105, 105, 105); 
}

