___CSS_LOADER_EXPORT___.push([module.id, `.proyecto-card {
    width: 600px;
    height: auto;
    border: 1px solid #ccc;
    border-radius: 20px;
    padding: 16px;
    text-align: center;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    background-color: rgba(255, 255, 255, 0.18);
    opacity: 0;
    transform: translateX(-200px);
    transition: opacity 1s ease, transform 2s ease;
  }
  
  .proyecto-card.visible {
    opacity: 1;
    transform: translateX(0); 
  }
  
  .proyecto-card.slide-right {
    transform: translateX(200px);
  }
  
  .proyecto-card.slide-right.visible {
    opacity: 1;
    transform: translateX(0);
  }
  
  .proyecto-titul {
    font-size: 2rem;
    color: #ff6600;
    margin: 0 0 10px 0;
    font-weight: 600;
    font-family: "DM Serif Display", serif;
    margin-left: 13rem;
  }
  
  .proyecto-descrip {
    font-size: 1rem;
    color: #666;
    margin-bottom: 15px;
  }
  
  .proyecto-carrusel {
    position: relative;
    width: 100%;
    height: 280px;
    overflow: hidden;
    margin-bottom: 10px;
  }
  
  .proyecto-imagen {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 8px;
    border: 1px solid black;
  }
  
  .proyecto-carrusel button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(0, 0, 0, 0);
    color: #ff6600;
    border: none;
    padding: 10px 15px;
    cursor: pointer;
    font-size: 3.5rem;
    border-radius: 4px;
    animation: moveButton 0.7s ease-in-out infinite;
  }
  
  .proyecto-carrusel button:first-of-type {
    left: -15px;
  }
  
  .proyecto-carrusel button:last-of-type {
    right: -15px;
  }
  
  @keyframes moveButton {
    0%, 100% {
      transform: translateY(-50%) translateX(0);
    }
    50% {
      transform: translateY(-50%) translateX(-5px);
    }
  }
  
  .proyecto-botones {
    display: flex;
    justify-content: space-around;
    margin-top: 10px;
  }
  
  .proyecto-boton3,
  .proyecto-boton4 {
    background: linear-gradient(to right, #ff7e29, #ff2600);
    color: white;
    border: none;
    text-decoration: none;
    padding: 8px 20px;
    border-radius: 30px;
    font-size: 1rem;
    text-align: center;
    display: inline-block;
    transition: background-color 0.3s ease;
    font-weight: 600;
  }
  
  .proyecto-boton3:hover,
  .proyecto-boton4:hover {
    transform: scale(1.05);
    color: black;
  }
  
  .proyecto-sin {
    color: red;
    font-weight: bold;
    animation: fadeIn 0.5s ease;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  .circulos {
    display: flex;
    justify-content: center;
    margin: 10px 0;
  }
  
  .circulo {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #ccc;
    margin: 0 5px;
    transition: background-color 0.3s ease;
  }
  
  .circulo.activo {
    background-color: #ff6600;
  }
  
  .proyecto-informativo {
    display: flex;
    justify-content: space-between;
  }
  
  .iconito { margin-bottom: 10px; margin-left: 10px; }
  .proyecto-icona { margin-right: 7px; }
  .proyecto-textico { font-size: 1rem; }
  
  .info-button{
    display: flex;
    font-weight: 600;
  }
  
  .proyecto-skills-overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.7);
    color: #fff;
    padding: 10px;
    text-align: center;
    opacity: 0;
    transform: translateY(100%);
    transition: opacity 0.5s ease, transform 0.5s ease;
    font-size: 1rem;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    display: flex;
    justify-content: start;
  }
  
  .proyecto-skills-overlay.show {
    opacity: 1;
    transform: translateY(0);
  }
  
  .proyecto-skill-item {
    display: flex;
    align-items: center;
    background-color: #ff6600;
    color: rgb(255, 255, 255);
    padding: 5px;
    border: 1px solid rgb(255, 255, 255);
    border-radius: 8px;
    font-size: 0.8rem;
    font-weight: bold;
    transition: background-color 0.3s ease;
    margin-right: 5px;
  }
  
  .proyecto-skill-icon {
    font-size: 1rem;
    margin-right: 3px;
  }
  
  @media (max-width: 760px) {
  
    .proyecto-card {
        width: 100%;
        padding: 12px;
        margin: 0 auto;
        border-radius: 15px;
    }
  
    .proyecto-titul {
        font-size: 1.5rem; 
        margin-left: 0;
    }
  
    .proyecto-descrip { font-size: 0.9rem; margin-bottom: 10px; }
    .proyecto-carrusel { height: 200px; }
    .proyecto-imagen { border-radius: 6px; }
  
    .proyecto-carrusel button {
        font-size: 2.5rem;
        padding: 5px 10px;
    }
  
    .proyecto-boton3,
    .proyecto-boton4 {
        font-size: 0.9rem;
        padding: 6px 16px;
    }
  
    .circulos {
        margin: 8px 0;
    }
  
    .circulo {
        width: 10px;
        height: 10px;
    }
  
    .proyecto-skills-overlay {
        font-size: 0.9rem;
        padding: 8px;
        justify-content: center;
    }
  
    .proyecto-skill-item {
        font-size: 0.7rem;
        padding: 4px;
        margin-right: 4px;
    }
  }
