___CSS_LOADER_EXPORT___.push([module.id, `.navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: .7rem 2rem;
    position: fixed;
    width: 100%;
    top: 0;
    transition: background-color 0.3s ease;
    z-index: 1000;
    background-color: rgba(255, 255, 255, 0.18);
    color: white;
    border-bottom: 1px solid rgb(105, 105, 105);
    animation: slideIn 1s ease forwards;
}

@keyframes slideIn {
    0% { transform: translateX(-10%) scale(0.9); opacity: 0; }
    100% { transform: translateX(0) scale(1); opacity: 1; }
}

.navbar-scrolled {
    background: linear-gradient(to right, #ff7e29, #ff2600);
}

.navbar-logo {
    font-size: 1.5rem;
    font-weight: bold;
}

.navbar-logo a {
    text-decoration: none;
}

.logo-white {
    color: white;
}

.logo-orange {
    background: linear-gradient(to right, #ff7e29, #ff2600);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.logo-black {
    color: black;
}

.navbar-links {
    display: flex;
    gap: 2rem;
}

.navbar-links a {
    text-decoration: none;
    font-weight: bold;
    color: white;
    transition: color 0.2s ease; /* Transición suave del color */
    font-size: 15px;
    position: relative; /* Necesario para posicionar el pseudo-elemento */
    box-sizing: border-box;
    padding-bottom: 3px; /* Espaciado entre el texto y el borde */
    cursor: pointer;
}

/* Si el bloque \`a:not([href]):not([class])\` no puede eliminarse */
a:not([href]):not([class]) {
    color: white; /* Sobrescribe el color heredado */
    text-decoration: none;
}

a:not([href]):not([class]):hover {
    color: #ff6600; /* Aplica el color naranja al hover */
}

/* Pseudo-elemento para el borde animado */
.navbar-links a::after {
    content: ''; /* Sin contenido visible */
    position: absolute;
    bottom: 0; /* Colocado al fondo del enlace */
    left: 0; /* Inicia desde la izquierda */
    width: 0%; /* Comienza vacío */
    height: 3px; /* Altura del borde */
    background-color: #ff6600; /* Color del borde por defecto (naranja) */
    transition: width 0.6s ease, background-color 0.3s ease; /* Efecto suave */
    z-index: -1; /* Para no interferir con el texto */
}

/* Hover: el borde se llena de izquierda a derecha */
.navbar-links a:hover::after {
    width: 100%; /* Se expande completamente */
}

/* Estilo para enlaces activos */
.navbar-links a.active {
    color: black;
}

/* Borde completo para enlaces activos */
.navbar-links a.active::after {
    background-color: black;
    width: 100%;
}

/* Hover en modo navbar-scrolled */
.navbar-scrolled .navbar-links a:hover::after {
    background-color: black; /* Cambia el color del borde a negro */
    color: black;
    width: 100%; /* Mismo efecto de llenado */
}

.navbar-scrolled .navbar-links a:hover{
    color: black;
}


@media (max-width: 768px) {

    .navbar-links a::after {
        display: none;
    }

    .navbar {
        justify-content: space-between;
        width: 100vw;
    }

    .navbar-links {
        display: none;
        flex-direction: column;
        width: 100%;
        align-items: center;
        background-color: rgb(0, 0, 0);
        position: fixed;
        top: 60px;
        left: 0;
        right: 0;
        gap: 0;
        z-index: 9999;
    }

    .navbar-links.active {
        display: flex;
    }

    .navbar-toggle {
        display: block;
        background: none;
        border: none;
        cursor: pointer;
        font-size: 1.5rem;
        color: white;
    }

    .navbar-links a {
        font-size: 18px;
        padding: 1rem;
        width: 100%;
        text-align: center;
        border: none;
        position: relative;
    }

    .navbar-links a:hover {
        background-color: #ff6600;
        color: rgb(0, 0, 0) !important;
        border: none;
    }

     .navbar-links a.active {
        background-color: #ff6600;
        color: black;
        border: none;
    }

    .navbar-scrolled .navbar-links a:hover {
        color: black;
        border: none;
    }
}

@media (min-width: 769px) {
    .navbar-toggle {
        display: none;
    }
}

@media (max-width: 480px) {
    .navbar {
        padding: 0.8rem;
    }

    .navbar-toggle {
        font-size: 1.2rem;
    }

    .navbar-links a {
        font-size: 16px;
        padding: 0.8rem;
    }
}

@media (max-width: 360px) {

    .navbar {
        padding: 0.6rem;
    }

    .navbar-toggle {
        font-size: 1rem;
    }

    .navbar-links a {
        font-size: 14px;
        padding: 0.6rem;
    }
}
