___CSS_LOADER_EXPORT___.push([module.id, `.skills-container {
    padding: 6rem 0 0 0;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.skills-content {
    display: flex;
    gap: 2rem;
    width: 100%;
    max-width: 1200px;
    background-color: rgba(255, 255, 255, 0.18);
    padding: 1.5rem;
    border-radius: 20px;
    border: 1px solid rgb(105, 105, 105);
}

.skills-left-column {
    flex: 4; 
    padding: 1rem 0;
}

.skills-right-column {
    flex: 3;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.18);
    border-radius: 20px;
    border: 1px solid rgb(105, 105, 105);
}

.skills-title {
    font-size: 3rem;
    margin-bottom: 4rem;
    font-family: "DM Serif Display", serif;
    color: white;
    border-bottom: .4rem solid #ff6600;
    display: flex;
    justify-content: center;
    align-items: center;
}

.skills-icon {
    color: #ff6600;
    margin-right: 10px;
    font-size: 3rem;
}

.skill-cont-table {
    background-color: rgba(255, 255, 255, 0.18);
    max-width: 775px;
    padding: 1.5rem;
    border-radius: 20px;
    border: 1px solid rgb(105, 105, 105);
}

.skills-table {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: center;
}

.skill-item {
    border: 1px solid white;
    padding: 10px;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    height: 150px;
    width: 120px;
    position: relative;
    overflow: hidden;
}

.skills-table img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.skill-text {
    display: block;
    position: absolute;
    bottom: -30px;
    left: 50%;
    transform: translateX(-50%);
    opacity: 0;
    transition: bottom 0.5s ease, opacity 0.5s ease;
    font-weight: bold;
    background: black;
    color: white;
    padding: 10% 50%;
    margin-bottom: 10%;
}

.skill-item:hover .skill-text {
    bottom: 10px;
    opacity: 1;
}

.responsive-image {
    width: 100%;
    max-width: 400px;
    border-radius: 10px;
    margin-bottom: 1rem;
}

.skills-description {
    font-size: 1rem;
    color: white;
    text-align: center;
    margin: 0;
    font-weight: 600;
}

.fade-in,
.fade-in-image,
.fade-in-paragraph {
    opacity: 0;
    transition: opacity 2s ease, transform 2s ease;
}

.fade-in {
    transform: translateY(80px);
}

.fade-in.visible {
    opacity: 1;
    transform: translateY(0);
}

.fade-in-image,
.fade-in-paragraph {
    transform: translateX(100px);
}

.fade-in-image.visible,
.fade-in-paragraph.visible {
    opacity: 1;
    transform: translateX(0);
}

@media (max-width: 855px) {

    .skills-table{
        justify-content: left;
    }

    .skill-item {
        width: 90px;
        height: 110px;
    }
}

@media (max-width: 768px) {
    .skills-content {
        flex-direction: column;
        gap: 1.5rem;
        width: 90vw;
    }

    .skills-left-column {
        order: 2;
        flex: 1;
    }

    .skills-right-column {
        order: 1;
        flex: 1;
    }

    .skills-title {
        font-size: 2.5rem;
        margin-bottom: 3rem;
    }

    .skills-description {
        font-size: 0.9rem;
        margin-bottom: 1rem;
    }

    .responsive-image {
        max-width: 100%;
    }

    .skill-item {
        width: 100px;
        height: 120px;
    }
}

@media (max-width: 500px) {

    .skills-table{
        justify-content: left;
    }

    .skill-item {
        width: 80px;
        height: 100px;
    }
}

@media (max-width: 390px) {

    .skills-table{
        justify-content: left;
        gap: 10px;
    }

    .skill-item {
        width: 70px;
        height: 90px;
    }
}
